@import '../../../../variables';

.ProfileForm {
  &.toAdd {
    display: flex; }
  &-header {
    width: 100%;
    padding: 15px;
    margin-bottom: 20px;
    box-sizing: border-box; }

  &-preview {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    .openForm {
      background: $white;
      margin-right: 20px; }

    form {
      .formBtns {
        margin-top: 40px;
        display: flex;
        justify-content: flex-end;
        button:first-child {
          background: $white;
          margin-right: 8px; } } } } }


@media (max-width: 599.5px) {
  .profileShippingAddress {
    &-preview {
      flex-direction: column;
      justify-content: center;
      align-items: flex-start; }

    .openForm {
      margin-left: 20px; } } }
